@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
html,
body {
  width: 100%;
  height: 100%;
}
body {
  align-items: center;
  display: flex;
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  letter-spacing: 0.05em;
  justify-content: center;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  align-items: center;
  display: flex;
  justify-content: center;
  min-height: 100%;
  text-align: center;
}
.App--Title {
  font-size: 1.5em;
  font-weight: normal;
  line-height: 0.9em;
}
.App--Title strong {
  font-weight: bold;
}
.App--TitleMyRole {
  white-space: nowrap;
}

